@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,300&display=swap);
html{box-sizing:border-box}*,*:before *:after{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"Roboto",sans-serif;color:#202020;background-color:#fafafa}.header{border-bottom:solid 1px #ca2100;background-color:#db4c3f;box-shadow:0 1px 2px rgba(0,0,0,0.15);height:44px;width:100%}.header img{width:44px}.header nav{height:100%;max-width:922px;display:flex;align-items:center;margin:auto}.content{display:flex;max-width:922px;margin:auto}.side{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;height:100vh;padding-top:74px;border-right:1px solid #f1f1f1;background-color:#fafafa}.side div{margin-bottom:10px;cursor:pointer;padding:15px}.side div.active{background-color:white}.side div .icon{margin-right:10px}.task{border-color:white;height:100vh;width:calc(922px-266px);padding:74px,20px,0}.task .add-task-btn{cursor:pointer}.task .add-task-btn .plus{color:#dd4b39;font-size:18px}.task .add-task-btn .add-task-text{color:#545454;font-size:14px;margin-left:15px}.add-task-dialog{margin-top:10px}.add-task-dialog input{width:100%;border:1px solid #ddd;background-color:white;border-radius:3px;height:35px;padding-left:10px;padding-right:10px}.add-task-dialog .add-task-actions-container{margin-top:10px;display:flex;justify-content:space-between}.add-task-dialog .add-task-actions-container .add-btn{background-color:#db4c13;color:white;border:1px solid transparent;margin-right:5px;font-weight:bold;padding:6px 12px 7px 12px;border-radius:3px !important}.add-task-dialog .add-task-actions-container .add-btn:disabled{background-color:gray}.add-task-dialog .add-task-actions-container .add-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.add-task-dialog .add-task-actions-container .cancel-btn{all:unset;color:#555;cursor:pointer;font-size:14px;margin:0px 5px}.task-items-container{margin-top:40px;padding:0 20px}.task-items-container .task-item{display:flex;justify-content:space-between}.task-items-container .task-item:not(:last-child){border-bottom:1px solid #db4c3f}

